import { Component, Input, OnInit } from '@angular/core';
import { SwiperConfigInterface } from "ngx-swiper-wrapper";
import { SharedCartService } from 'projects/e-commerce/src/app/sharedServices/cartDataService';

@Component({
  selector: 'app-featured',
  templateUrl: './featured.component.html',
  styleUrls: ['./featured.component.scss'],
})
export class FeaturedComponent implements OnInit {


  constructor(private sharedCartService:SharedCartService) { 


  }

  ngOnInit(): void {

  }
  public showHidePopup:boolean = false;

  addToCart(val:any){
    this.sharedCartService.changeMessage({
      anee: val +1
    });
  }
  currentRate = 3.6;
  
  @Input() slide:any;
  productItems:object = {};

  public FEATURED_SWIPER_CONFIG: SwiperConfigInterface = {
		observer: true,
		direction: "horizontal",
		threshold: 50,
		slidesPerView: 5,
		keyboard: true,
		mousewheel: false,
		scrollbar: false,
		navigation: true,
    pagination: false,
    breakpoints:{
      0:{
        slidesPerView: 1,
        spaceBetween: 0,
      },
      540: {
        slidesPerView: 2,
        spaceBetween: 20,
      },
      768: {
        slidesPerView: 3,
        spaceBetween: 20,
      },
      1024: {
        slidesPerView: 3,
        spaceBetween: 20,
      },
      1200: {
        slidesPerView: 4,
        spaceBetween: 20,
      },
   }
};
imgView(val:any):void {
  this.productItems = val;
  this.showHidePopup = true;

}
closePoup(data:boolean) {
  this.showHidePopup = data;
}

}
